import React, {Component} from 'react';
import {Draggable} from "react-beautiful-dnd";
import styled from "styled-components";

const DragItem = styled.div`
    padding : 5px;
    z-index : 999;
    display: flex;
    height : 100px;
    align-item : center;
    justify-content : center;
    flex-direction: column;
    border : 1px solid #000;
    margin-bottom : 10px;
    background-color : ${props => (props.isDragging) ? 'lightgreen' : '#ccc'}
`;

class ColumnItem extends Component {

    render() {
        return (
            <Draggable key={this.props.key} draggableId={this.props.task.id} index={this.props.index}>
                {(provided, snapshot) => (
                    <div className="drag-box_item"
                         ref={provided.innerRef}
                         isDragging={snapshot.isDragging}
                         {...provided.draggableProps}
                         {...provided.dragHandleProps}>
                        {/*<i className="material-icons big" style={{fontSize : 50}}>account_circle</i>*/}
                        <img src="/img/icons/user.png" style={{width : 45 , zIndex : 99}} />
                        <p style={{padding : 'noen' , margin : 0}}>{this.props.task.content}</p>
                    </div>
                )}
            </Draggable>

        )
    }
}

export default ColumnItem;