import React, {useEffect , useHistory} from "react";
import {Link , Redirect} from "react-router-dom";
import {useTranslation} from "react-i18next";
import M from "materialize-css";
import {connect} from "react-redux"
import {TOKEN_KEY} from '../../../../services/Authentication/Types'
import $ from 'jquery'
import history from '../../../../entity/Model/History/BrowserHistory'

function SideBar(props){
    // console.log("props : " , props)
    // const history = useHistory();
    // history.push('/signin')
    useEffect(() => {
        var elems = document.querySelectorAll('.sidenav');
        M.Sidenav.init(elems);
        $("#logout").click(() =>{
            localStorage.removeItem(TOKEN_KEY);
            setTimeout(() =>{
                history.push('/signin');
                window.location.reload();
                // <Redirect to={'/signin'} />
            },400)
        })

    })


    const {t} = useTranslation();
    return (
        <aside id="left-sidebar-nav">
            <ul id="slide-out" className="side-nav fixed leftside-navigation">
                <li className="user-details cyan darken-2">
                    <div className="row">
                        <div className="col col s4 m4 l4">
                            <img src="/img/avatar/avatar-13.png" alt=""
                                 className="circle responsive-img valign profile-image cyan"/>
                        </div>
                        <div className="col col s8 m8 l8">
                            <p className="user-roal" id="user-roel-det">Emran rastadi sdvsdvvvvvvvvvvvvvvvvvvv</p>
                            <p className="user-roal">full admin</p>
                        </div>
                    </div>
                </li>
                <li className="no-padding">
                    <ul className="collapsible" data-collapsible="accordion">
                        <li className="bold">
                            <a className="waves-effect waves-cyan">
                                <Link to="/admin-top">
                                    <i className="material-icons">pie_chart_outlined</i>
                                    <span className="nav-text" data-localize="menu.dashboard">{t('Dashboard.l')}</span>
                                </Link></a>
                        </li>
                        <li className="bold">
                            <a className="waves-effect waves-cyan">
                                <Link to="/user-manage">
                                    <i className="material-icons">person_outline</i>
                                    <span className="nav-text" data-localize="menu.users">{t('Users.l')}</span>
                                </Link></a>
                        </li>


                        {/*<li className="bold">*/}
                        {/*    <a className="waves-effect waves-cyan">*/}
                        {/*        <Link to="/doctor/package-list">*/}
                        {/*            <i><img src="/img/icons/doctor.png"/></i>*/}
                        {/*            <span className="nav-text" data-localize="menu.doctors">{t('package.l')}</span>*/}
                        {/*        </Link>*/}
                        {/*    </a>*/}
                        {/*</li>*/}


                        <li className="bold">
                            <a className="waves-effect waves-cyan">
                                <Link to="/setting-file">
                                    <i className="material-icons">settings</i>
                                    <span className="nav-text">{t('setting-file.l')}</span>
                                </Link>
                            </a>
                        </li>
                        <li className="bold">
                            <a className="waves-effect waves-cyan">
                                <Link to="/chat">
                                    <i className="material-icons">textsms</i>
                                    <span className="nav-text">{t('Message.l')}</span>
                                </Link>
                            </a>
                        </li>
                        <li className="bold">
                            <a className="waves-effect waves-cyan">
                                <Link to="/">
                                    <i className="material-icons">report</i>
                                    <span className="nav-text">{t('Report.l')}</span>
                                </Link>
                            </a>
                        </li>
                        {/*<li className="bold">*/}
                        {/*    <a className="waves-effect waves-cyan">*/}
                        {/*        <Link to="/doctor/patient-list">*/}
                        {/*            <i><img src="/img/icons/admin.png"/></i>*/}
                        {/*            <span className="nav-text">{t('Patient.l')}</span>*/}
                        {/*        </Link>*/}
                        {/*    </a>*/}
                        {/*</li>*/}
                        <li className="bold">
                            <a className="waves-effect waves-cyan">
                                <Link to="/">
                                    <i className="material-icons">show_chart</i>
                                    <span className="nav-text">{t('Factor.l')}</span>
                                </Link>

                            </a>
                        </li>
                        <li className="bold" id="logout">
                            <a className="waves-effect waves-cyan">
                                    <i className="material-icons">keyboard_tab</i>
                                    <span className="nav-text">{t('Logout.l')}</span>
                                </a>
                        </li>
                    </ul>
                </li>
            </ul>
            <a href="#" data-activates="slide-out"
               className="sidebar-collapse btn-floating btn-medium waves-effect waves-light hide-on-large-only">
                <i className="material-icons">menu</i>
            </a>
        </aside>
    )
}

const mapStateToProps = (state) => {
    console.log('state : ' , state);
    // return {state : state}
}

export default connect(mapStateToProps)(SideBar);