import React, {Component} from 'react';
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
import {Link} from "react-router-dom";
import Board from "./MiniComponent/Board";
import Item from "./MiniComponent/Item";
import InitialData from "./data/InitialData";
import Columns from "./MiniComponent/Columns";
import {DragDropContext} from "react-beautiful-dnd";

// import Columns from "./dragingItem/Columns";

class UsersControl extends Component {

    state = InitialData;

    onDragStart = () => {
        document.body.style.color = 'orange';
    }

    onDragEnd = result => {
        // console.log("result : " ,result);

        const {destination, source, draggableId} = result;

        if (!destination) {
            return;
        }
        if (destination.droppableId === source.droppableId && destination.index === source.index) {
            return;
        }

        const start = this.state.columns[source.droppableId];
        const finish = this.state.columns[destination.droppableId];
        if (start === finish) {

            const newTaskId = Array.from(start.taskIds);
            newTaskId.splice(source.index, 1);
            newTaskId.splice(destination.index, 0, draggableId);

            const newColumn = {
                ...start,
                taskIds: newTaskId
            };

            const newState = {
                ...this.state,
                columns: {
                    ...this.state.columns,
                    [newColumn.id]: newColumn
                }
            }
            this.setState(newState);
            return;
        }


        const startTaskIds = Array.from(start.taskIds);
        startTaskIds.splice(source.index, 1);
        const newStart = {
            ...start,
            taskIds: startTaskIds
        };

        const finishTaskIds = Array.from(finish.taskIds);
        finishTaskIds.splice(destination.index, 0, draggableId);
        const newFinish = {
            ...finish,
            taskIds: finishTaskIds
        };
        const newState = {
            ...this.state,
            columns: {
                ...this.state.columns,
                [newStart.id]: newStart,
                [newFinish.id]: newFinish
            }
        }

        this.setState(newState);
        return;

    }

    render() {

        return (
            <div>
                <div id="loader-wrapper">
                    <div id="loader"></div>
                    <div className="loader-section section-left"></div>
                    <div className="loader-section section-right"></div>
                </div>

                <Header/>
                <div id="main">
                    <div className="wrapper">


                        <SideBar/>

                        <div id="back_shado"></div>

                        <div className="container">

                            <DragDropContext
                                onDragEnd={this.onDragEnd}
                                onDragStart={this.onDragStart}
                            >
                                {this.state.columnOrder.map(colId => {
                                    const column = this.state.columns[colId];
                                    const task = column.taskIds.map(taskId => this.state.task[taskId]);
                                    return <Columns key={task.id} column={column} tasks={task}/>;
                                })}
                            </DragDropContext>


                        </div>
                    </div>
                </div>
            </div>
        )

    }


}

export default UsersControl;