import React, {Component} from 'react';
import ColumnItem from "./ColumnItem";
import {Droppable} from "react-beautiful-dnd";


class Columns extends Component {
    render() {
        return (

            <div className="row" style={{paddingTop : 55 , position : "relative"}}>
                <div className="col s12">

                    <p id="drag_box_title" className="card-title center-align">{this.props.column.title}</p>

                    <div className="main_box_dragable">

                        <div className="line_grad"></div>



                            <Droppable droppableId={this.props.column.id} direction="horizontal">
                                {(provided) => (
                                    <div className="dropable_main"
                                         ref={provided.innerRef}
                                         {...provided.droppableProps}>
                                        {this.props.tasks.map((task, index) => <ColumnItem index={index} key={task.id}
                                                                                           task={task}/>)}
                                        {provided.placeholder}
                                    </div>
                                )}
                            </Droppable>

                    </div>
                </div>
            </div>

            // <div className="card">
            //     <Title>{this.props.column.title}</Title>
            //     <Droppable droppableId={this.props.column.id}>
            //         {
            //             provided => (
            //                 <TaskList
            //                     innerRef={provided.innerRef}
            //                     {...provided.droppableProps}>
            //                     {this.props.tasks.map((task, index) => <ColumnItem index={index} key={task.id}
            //                                                                        task={task}/>)}
            //                 </TaskList>
            //             )
            //         }
            //
            //     </Droppable>
            //
            // </div>

            /*
            <div className="row">
                    <div className="col s12 m6">
                        <div className="card blue-grey darken-1">
                            <div className="card-content white-text">
                                <span className="card-title">{this.props.column.title}</span>

                                <Droppable droppableId={this.props.column.id}>
                                    {
                                        provided => (
                                            <div className="list_items"
                                            innerRed={provided.innerRef}
                                                 {...provided.droppableProps}>
                                                {this.props.tasks.map((item , index) => <ColumnItem index={index} key={item.id} task={item} />)}
                                                {provided.placeholder}
                                            </div>
                                        )
                                    }
                                </Droppable>


                            </div>
                        </div>
                    </div>
                </div>
            */

        )
    }
}

export default Columns;